/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  color: #333;
  line-height: 1.6;
}

#root {
  margin: 0;
  padding: 0;
}

/* Optional: Styling for any other elements */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Arial', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

